let baseUrl = 'http://10.18.200.188:8889/'
let ajax = $.ajax
let intercept = function (ajaxOpts) {
//	return  new Promise (function (resolve,reject) {
//		 ajax(ajaxOpts).then(function (res) {
//		 	resolve(res)
//		 }).fail(function (err) {
//		 	reject(err)
//		 })
//	})
	let promise = $.Deferred()
	 ajax(ajaxOpts).then(function (res) {
	 	promise.resolve(res)
	 }).fail(function (err) {
	 	promise.reject(err)
	 })
	return promise.promise()
}
let apis = {
	getPhoneNos (data) {
		return intercept({
			type:'get',
			url:baseUrl + 'phones'
		})
	},
	registe (data) {
		return intercept({
			type:'post',
			data,
			url:baseUrl + 'register'
		})
	},
	login (data) {
		return intercept({
			type:'post',
			data,
			url:baseUrl + 'login'
		})
	},
	getUserList (data) {
		return intercept({
			type:'get',
			data,
			url:baseUrl + 'getUserList'
		})
	},
	getUserInfo (user_id) {
		return intercept({
			type:'get',
			data:{user_id},
			url:baseUrl + 'getUserInfo'
		})
	},
	publishTopic (data,userId) {
		return intercept({
			type:'post',
			data,
			url:baseUrl + 'publishTopic/' + userId
		})
	},
	getTopicList (data) {
		return intercept({
			type:'get',
			data,
			url:baseUrl + 'getTopics'
		})
	},
	zanTopic (id) {
		return intercept({
			type:'get',
			data:{id},
			url:baseUrl + 'zanTopic'
		})
	},
	updataHeadImg (data) {
		return intercept({
			type:'post',
			data,
			url:baseUrl + 'updataHeadimg'
		})
	},
	uploadImg (blob,path) {
		var formData = new FormData()
		formData.append('img_key',blob,blob.name)
		return $.ajax({
			type:'post',
			data:formData,
//			processData: false,
//          contentType:undefined,
//          mimeType:"multipart/form-data",
            processData: false,
    		contentType: false,
			url:baseUrl + 'uploadMedia/' + path
		})
	}
}

export default apis




